"use client";

import React, { useState, useEffect, ReactNode } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  YadakchiAdminLayout,
  InfoCard, // ุงููพูุฑุช ุตุญุญ
  Label,
  ActionToolbar,
  EntityHeader,
  Badge,
} from "@my-ds/design-system";

import { getShopById, type Shop } from "../../../services/shopsService";
import { translateStatus } from "../../../utils/translations";
import { FaBan } from "react-icons/fa";
import { CheckCircleIcon } from "@heroicons/react/24/solid";

interface ShopDetailsPageProps {
  params: { id: string; }
}

export default function ShopDetailsPage({ params }: ShopDetailsPageProps) {
  const { id } = params;
  const router = useRouter();
  
  const [shop, setShop] = useState<Shop | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  useEffect(() => {
    if (!id) return;
    const fetchShop = async () => {
      try {
        setIsLoading(true);
        setError(null);
        const shopData = await getShopById(id);
        setShop(shopData);
      } catch (err) {
        setError(err instanceof Error ? err.message : "ฺฉ ุฎุทุง ูุงุดูุงุฎุชู ุฑุฎ ุฏุงุฏ.");
      } finally {
        setIsLoading(false);
      }
    };
    fetchShop();
  }, [id]);

  const renderContent = () => {
    if (isLoading) return <p>ุฏุฑ ุญุงู ุจุงุฑฺฏุฐุงุฑ ุงุทูุงุนุงุช ูุฑูุดฺฏุงู...</p>;
    if (error) return <p style={{ color: 'red' }}>ุฎุทุง: {error}</p>;
    if (!shop) return <p>ุงุทูุงุนุงุช ูุฑูุดฺฏุงู ุงูุช ูุดุฏ.</p>;

    return (
      // โจ --- ุชุบุฑุงุช ุงุตู ุฏุฑ ุงูุฌุง ูุณุชูุฏ ---
      <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
        <EntityHeader
            title={shop.shopTitle}
            avatarSrc={shop.logo ? `https://api.yadakchi.com${shop.logo}` : undefined}
        >
            <Badge 
                label={translateStatus(shop.registrationStatus)} 
                variant={shop.registrationStatus === 'Confirmed' ? 'success' : 'warning'}
            />
        </EntityHeader>

        <ActionToolbar onBackClick={() => router.back()}>
            <button>ูุฑุงุด</button>
        </ActionToolbar>

        {/* ฑ. ูุง ุญุงูุง ุงุฒ prop ุจู ูุงู "items" ุงุณุชูุงุฏู ูโฺฉูู */}
        <InfoCard 
          title="ุงุทูุงุนุงุช ุงุตู"
          items={[
            { label: "ูุงู ูุฑูุดูุฏู", value: `${shop.name} ${shop.lastname}` },
            { label: "ูุจโุณุงุช", value: shop.website || "-" },
            { label: "ุชูุถุญุงุช", value: shop.description || "-" },
          ]}
        />
        
        <InfoCard 
          title="ุงุทูุงุนุงุช ุชูุงุณ"
          items={[
            { label: "ุชููู", value: shop.tell || "-" },
            { label: "ุดูุงุฑู ููุฑุงู", value: shop.phoneNumber || "-" },
          ]}
        />
      </div>
    );
  };

  return (
    <YadakchiAdminLayout currentPath={pathname} renderLink={renderLink}>
      {renderContent()}
    </YadakchiAdminLayout>
  );
}